<style>
  .p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .img-w {
    width: 80px;
    height: 80px;
  }
  .img-w>img {
    width: 80px;
  }
  .p-m-c-r {
    padding: 0 10px;
  }
  .erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .g-m-r {
    position: relative;
  }

  .mt-show-w {
    width: 120px;
    height: 120px;
    position: relative;
  }
  .mt-show-w>img {
    width: 120px;
  }
  .t-m-w, .b-m-w {
    height: 24px;
    background: rgba(0, 0, 0, .5);
    text-align: center;
    line-height: 24px;
    font-size: 10px;
    color: #fff;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }
  .t-m-w {
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
  }
  .b-m-w {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }
  .sp-line {
    width: 100%;
    height: 1px;
    background-color: #ececec;
    margin: 10px 0;
  }

  .error-t-w {
    line-height: 32px;
    margin-bottom: 10px;
    background-color: #F4ACAE;
    padding-left: 10px;
    color: #555;
  }
  .error-t-w .c-btn-w {
    line-height: 32px;
    padding: 0 10px;
    float: right;
    cursor: pointer;
  }
</style>

<template>
  <div>
    <div class="error-t-w" v-show='isShowTip'>
      <i class="el-icon-warning"></i>注意：顺丰标品，请使用添加顺丰标品入口添加，系统会校验顺丰标品资料是否维护正确，确保系统能够要货成功
      <span class="c-btn-w"><i class="el-icon-close" @click="isShowTipHandel"></i></span>
    </div>
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary" @click="goAddGoodsPage" size="mini" icon="el-icon-plus">添加商品</el-button>
        <el-button type="primary" @click="batchAddGoods" size="mini" icon="el-icon-circle-plus-outline">批量添加</el-button>
        <el-button type="primary" @click="batchAddSFGoods" size="mini" icon="el-icon-plus">添加顺丰标品</el-button>
      </div>
      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <region-city-store @StoreGroupChange="StoreGroupChange" :showGroup="false" :showStore="false" :defaultCityId="searchContent.cityId"></region-city-store>
          <el-form-item label="商品分类">
            <el-cascader
              placeholder="请选择分类"
              class="search-input"
              :options="categoryList"
              change-on-select
              size="small"
              clearable
              filterable
              @change="doCategorysChangeHandel"
              :props="cateDefaultProps"
              v-model="searchContent.categoryIdList">
            </el-cascader>
          </el-form-item>
          <el-form-item label="SKU编码/商品名称">
            <goods-select ref="GoodsSelect" @DoSelectGoodsHandel="DoSelectGoodsHandel"></goods-select>
          </el-form-item>
          <el-form-item label="标签">
           <el-select class="search-input" v-model="searchContent.markType" filterable placeholder="请选择标签" clearable size="small" @change="doSearchHandel(true)">
              <el-option
                v-for="item in markTypeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="是否为供应商专供">
           <el-select class="search-input" v-model="searchContent.vendorType" filterable placeholder="是否为供应商专供" clearable size="small" @change="doSearchHandel(true)">
              <el-option
                v-for="item in vendorTypeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
            <el-button icon="el-icon-download" :loading="exportLoading" @click="exportData" size="mini">导出</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    
    <!-- 搜索结果 -->
    <div class="table-wraper">
      <div class="batch-handel-w">
        <el-button class="sup-mini" size="mini" icon="el-icon-edit" :disabled="checkedList.length === 0" @click="setMarkType">设置标签</el-button>
        <el-button class="sup-mini" size="mini" icon="el-icon-delete" :disabled="checkedList.length === 0" @click="deleteMarkType">删除标签</el-button>
        <el-button class="sup-mini" size="mini" icon="el-icon-edit" :disabled="checkedList.length === 0" @click="importStore">发布及时达</el-button>
        <el-button class="sup-mini" size="mini" icon="el-icon-delete" :disabled="checkedList.length === 0" @click="batchRemove">批量删除</el-button>
      </div>
      <el-table
        class="fixed-height"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        @select="handleSelect"
        @select-all="handleSelectAll"
        size="small">
        <el-table-column
          type="selection"
          align="center">
        </el-table-column>
        <el-table-column min-width="250px" label="商品信息">
          <template slot-scope="scope">
            <div class="p-m-w">
              <div class="img-w g-m-r">
                <p class="t-m-w" v-show="scope.row.topMark">{{scope.row.topMark}}</p>
                <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                <img v-else src="/shop/static/img_error.png" alt="">
                <p class="b-m-w"  v-show="scope.row.bottomMark">{{scope.row.bottomMark}}</p>
              </div>
              <div class="p-m-c-r">
                <div>{{scope.row.goodsName || '--'}}</div>
                <div class="erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
                <div class="erp-p-no">商品ID：{{scope.row.platformCityGoodsId || '--'}}</div>
                <div v-if="scope.row.sfGoodsDesc">
                  <el-tag size="mini">顺丰标品</el-tag>
                  <el-tooltip 
                    v-if="!scope.row.sfGoodsValidStatus" 
                    class="item" 
                    effect="dark" 
                    :content="scope.row.sfGoodsDesc" 
                    placement="top-start">
                    <span style="color: red;">校验异常</span>
                  </el-tooltip>
                </div>
                <div v-if="scope.row.vendorType === 1">
                  <el-tag
                    size="mini"
                    type="warning"
                    effect="dark">
                    供应商专供
                  </el-tag>
                </div>
              </div>
            </div>
          </template> 
        </el-table-column>
        <el-table-column min-width="120px" prop="spec" label="规格"></el-table-column>
        <el-table-column min-width="120px" prop="regionName" label="区域"></el-table-column>
        <el-table-column min-width="120px" prop="categoryName" label="分类"></el-table-column>
        <el-table-column min-width="120px" prop="price" label="区域售价"></el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="140">
          <template slot-scope="scope" size="small">
            <div>  
              <el-button @click="editItem(scope.row)" type="text" size="mini" icon="el-icon-edit">编辑</el-button>
              <el-button @click="deleteItem(scope.row)" type="text" size="mini" icon="el-icon-delete">删除</el-button>
            </div>
            <div>
              <el-button @click="showStoreState(scope.row)" type="text" size="mini" icon="el-icon-view">查看门店状态</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <choose-store ref="chooseStore" @confirmHandel="confirmHandel"></choose-store>

    <el-dialog title="批量设置标签" :visible.sync="dialogFormVisible" width='460px'>
      <el-form ref="form" :model="setMarkTypeParams" label-width="60px">
        <el-form-item label="角标">
          <el-input v-model="setMarkTypeParams.topMark" class="search-input" size="small" placeholder="请输入角标"></el-input>
          <span>限制6个字</span>
        </el-form-item>
        <el-form-item label="底标">
          <el-input v-model="setMarkTypeParams.bottomMark" class="search-input" size="small" placeholder="请输入底标"></el-input>
          <span>限制10个字</span>
        </el-form-item>
        <el-form-item label="预览">
          <div class="mt-show-w">
            <p class="t-m-w" v-show="setMarkTypeParams.topMark">{{setMarkTypeParams.topMark}}</p>
            <img v-if="setMarkTypeParams.imgUrl && setMarkTypeParams.imgUrl != ''" :src="setMarkTypeParams.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
            <img v-else src="/shop/static/img_error.png" alt="">
            <p class="b-m-w"  v-show="setMarkTypeParams.bottomMark">{{setMarkTypeParams.bottomMark}}</p>
          </div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="doSetMarkTypeConfirmHandel" size="mini" :disabled="isDisabled">确 定</el-button>
      </div>
    </el-dialog>
    
    <el-dialog title="批量删除标签" :visible.sync="dialogDelFormVisible" width='460px'>
      <el-form ref="form" :model="delMarkTypeParams" label-width="60px">
        <el-checkbox v-model="delMarkTypeParams.isDeleteTopMark">删除角标</el-checkbox>
        <el-checkbox v-model="delMarkTypeParams.isDeleteBottomMark">删除底标</el-checkbox>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogDelFormVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="doDelMarkTypeConfirmHandel" size="mini" :disabled="isDisabled">确 定</el-button>
      </div>
    </el-dialog>
    
    <!-- 批量改价 -->
    <batch-update-price ref="batchUpdatePrice" @doSearchHandel="doSearchHandel"></batch-update-price>
    <!-- 批量添加商品 -->
    <batch-add-goods ref="batchAddGoods" @doSearchHandel="doSearchHandel"></batch-add-goods>

    <excel-add ref="excelAdd" @doConfirmHandel="doConfirmHandel"></excel-add>
    <el-dialog 
      title="批量导入失败" 
      :visible.sync="showTipErrorMsg" 
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      width='450px'>
      <div>
        部分商品不符合要求，请按要求修改后再重新导入。<el-button type="text" size="mini" @click="downLoadErrorMsg">下载查看失败原因</el-button>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showTipErrorMsg = false" size="mini">关 闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import ChooseStore from "@/components/common/StoreCommon/RegionChooseStore"
  import BatchUpdatePrice from "@/components/GoodsMgt/common/BatchUpdatePriceForPlat"
  import GoodsSelect from "@/components/Common/GoodsCommon/SearchByRegion"
  import BatchAddGoods from "@/components/GoodsMgt/common/BatchAddGoods"
  import exportSheet from '@/utils/exportExcel'
  import RegionCityStore from '@/components/Common/StoreCommon/RegionCityStore';
  import AddByExcel from "@/components/Common/Excel/AddByExcel"
  import * as utils from '@/utils/utils'
  import config from '@/utils/config'
  const presaleGoodsMgt = {
    // 获取三方平台店内分类
    getCategoryTree: '/plateform/a/category/getCategoryTree',
    // 预售商品展示
    presaleSearch: '/plateform/a/platformCityGoods/presale/list',
    // 删除预售商品-支持批量
    presaleRemove: '/plateform/a/platformCityGoods/presale/remove',
    // 区域商品选择-商品列表
    selectCityGoods: '/plateform/a/platformCityGoods/presale/selectCityGoods',
    // 设置标签
    saveMark: '/plateform/a/platformCityGoods/presale/mark/saveMark',
    // 删除标签
    updateMark: '/plateform/a/platformCityGoods/presale/mark/updateMark',
    // 商品引入门店
    batchStoreImport: '/plateform/a/platformCityGoods/presale/store/import',
    // 校验批量添加顺丰商品
    checkSFBatchInsert: '/plateform/a/platformCityGoods/presale/checkSFBatchInsert',
    // 批量添加平台商品
    batchInsert: '/plateform/a/platformCityGoods/presale/batchInsert'
  }
  export default {
    data() {
      return {
        pageSizes: config.pageSizes10,
        isDisabled: false,
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          DIFFCITY: true,
          regionCode: Storage.get('regionCode') || '', // 区域ID
          cityId: '',
          queryString: '', // 查询条件
          platformCityGoodsIds: [], // 平台区域商品ID
          saleType: 4, // 售卖类型：1：常规；2：预售；3:长周期预售，4：及时达(查询对应售卖类型数据该字段必传，不传入择默认全部)
          platformId: 5, // 平台类型：1-有赞；2-京东；3-美团；4-饿百；5-商城
          categoryIdList: [],
          markType: '', // 标签：1-角标；2-底标
          categoryId: '', // 商品分类ID
          vendorType: ''
        },
        // 默认分类参数
        cateDefaultProps: {
          children: 'categoryVoList',
          label: 'categoryName',
          value: 'categoryId'
        },
        markTypeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '角标',
            value: 1
          },
          {
            label: '底标',
            value: 2
          }
        ],
        categoryList: [], // 商品分类id
        tableData: [], // 表格中查询结果数据
        // 多选选中的商品
        checkedList: [],
        // 门店
        storeOptions: [],
        // 弹窗是否显示
        isShow: false,

        // 弹窗显示 
        dialogFormVisible: false,
        setMarkTypeParams: {
          imgUrl: '',
          platformCityGoodsIdList: [], // 预售商品ID列表
          bottomMark: '', // 底标
          topMark: '' // 角标
        },
        dialogDelFormVisible: false,
        delMarkTypeParams: {
          platformCityGoodsIdList: [], // 预售商品ID列表
          isDeleteBottomMark: false,
          isDeleteTopMark: false,
          isBottomMark: 0, // 底标
          isTopMark: 0 // 角标
        },
        exportLoading: false,

        useType: 3,
        tempConfig: {
          goodsNo: 'SKU编码（必填）',
          platformCityGoodsName: '商品名称（必填，限制30个汉字）',
          presalePrice: '区域售价（必填，保留小数点后2位）',
          bigImageUrl: '商品大图url（选填）'
        },
        hasErrorMsgData: [],
        showTipErrorMsg: false,
        isShowTip: true, // 是否显示操作提示，默认显示f
        vendorTypeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '是',
            value: 1
          },
          {
            label: '否',
            value: 2
          }
        ]
      }
    },
    created() {
      // 三方平台店内分类
      this.getCategoryId()
    },
    mounted() {
      let searchContentMesg = Storage.get("searchTimelyGoodsContent")
      if(this.$route.meta.keepAlive && searchContentMesg) {
        this.searchContent = searchContentMesg.searchContent
        this.pageNum = searchContentMesg.pageNum

        Storage.remove('searchTimelyGoodsContent')
      }
      this.doSearchHandel()

      // 商品筛选组件初始化
      this.goodsSelectInit()
    },
    components: {
      "choose-store": ChooseStore,
      "batch-update-price": BatchUpdatePrice,
      "goods-select": GoodsSelect,
      "batch-add-goods": BatchAddGoods,
      "region-city-store": RegionCityStore,
      "excel-add": AddByExcel
    },
    methods: {
      StoreGroupChange(param) {
        this.searchContent.regionCode = param.regionCode
        this.searchContent.cityId = param.cityId
        this.doSearchHandel(true)
      },
      /**
       * [goAddGoodsPage 添加预售商品]
       * @return {[type]} [description]
       */
      goAddGoodsPage() {
        this.$router.push("/timely_goods/add_goods")
      },
      /**
       * [getCategoryId 三方平台店内分类]
       * @return {[type]} [description]
       */
      getCategoryId() {
        this.$http.post(presaleGoodsMgt.getCategoryTree, {DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                if(item.categoryVoList && item.categoryVoList.length === 0) {
                  item.categoryVoList = null
                }
              }
              this.categoryList = res.data
            }else {
              this.categoryList = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(三方平台店内分类):", e.message)
        })
      },
      /**
       * [doCategorysChangeHandel 分类发生变化]
       * @return {[type]} [description]
       */
      doCategorysChangeHandel() {
        // 商品筛选组件初始化
        this.goodsSelectInit()

        this.doSearchHandel(true)
      },
      /**
       * [goodsSelectInit 商品筛选组件初始化]
       * @return {[type]} [description]
       */
      goodsSelectInit() {
        if(this.$refs.GoodsSelect) {
          let _val = {
            queryString: this.searchContent.queryString,
            categoryIdList: this.searchContent.categoryIdList,
            saleType: this.searchContent.saleType,
            platformId: this.searchContent.platformId,
            platformCityGoodsIds: this.searchContent.platformCityGoodsIds
          }
          this.$refs.GoodsSelect.setDefaultMesg(_val)
        }
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandels 搜索]
       * @param  {[type]} _params [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        // 将选中数据重置为初始值
        this.checkedList = []

        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }

        this.$http.post(presaleGoodsMgt.presaleSearch, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            this.tableData = res.data && res.data.result && res.data.result.length ? res.data.result : []
            this.total = res.data.total
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })

      },
      /**
       * [editItem 编辑]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      editItem(_row) {
        // 设置搜索条件
        let _paramMesg = {
          searchContent: this.searchContent,
          pageNum: this.pageNum
        }
        Storage.set("searchTimelyGoodsContent", _paramMesg)

        this.$router.push("/timely_goods/edit_goods/"+`${_row.platformCityGoodsId}`)
      },
      /**
       * [deleteItem 删除]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      deleteItem(_row) {
        let platformCityGoodsIdsArr = []
        platformCityGoodsIdsArr.push(_row.platformCityGoodsId)
        this.$confirm('您确定要删除所选商品吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(presaleGoodsMgt.presaleRemove, {platformCityGoodsIds: platformCityGoodsIdsArr, DIFFCITY: true}).then(res => {
            if(res.code === 0) {
              this.$message.success("删除成功")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [batchRemove 批量删除商品]
       * @return {[type]} [description]
       */
      batchRemove() {
        let platformCityGoodsIdsArr = []
        for(let item of this.checkedList) {
          platformCityGoodsIdsArr.push(item.platformCityGoodsId)
        }
        this.$confirm('您确定要删除所选商品吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(presaleGoodsMgt.presaleRemove, {platformCityGoodsIds: platformCityGoodsIdsArr, DIFFCITY: true}).then(res => {
            if(res.code === 0) {
              this.$message.success("删除成功")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [setMarkType 设置标签]
       */
      setMarkType() {
        this.setMarkTypeParams = {
          imgUrl: this.checkedList[0].imgUrl,
          platformCityGoodsIdList: [], // 预售商品ID列表
          bottomMark: '', // 底标
          topMark: '' // 角标
        }
        this.dialogFormVisible = true
      },
      /**
       * [deleteMarkType 删除标签]
       * @return {[type]} [description]
       */
      deleteMarkType() {
        this.delMarkTypeParams = {
          platformCityGoodsIdList: [], // 预售商品ID列表
          isDeleteBottomMark: false,
          isDeleteTopMark: false,
          isBottomMark: 0, // 底标
          isTopMark: 0 // 角标
        }
        this.dialogDelFormVisible = true
      },
      /**
       * [presaleStatusFormatter 格式化状态]
       * @return {[type]} [description]
       */
      presaleStatusFormatter(row, column, cellValue, index) {
        if(cellValue === 0) {
          return '已失效'
        }else if(cellValue === 1) {
          return '未开始'
        }else if(cellValue === 2) {
          return '进行中'
        }else if(cellValue === 3) {
          return '售罄'
        }

      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArrayBykey(row.platformCityGoodsId, 'platformCityGoodsId', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('platformCityGoodsId', row.platformCityGoodsId, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.platformCityGoodsId] ? '' : hash[next.platformCityGoodsId] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {        
          // 全选
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 去重
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.platformCityGoodsId] ? '' : hash[next.platformCityGoodsId] = true && item.push(next)
            return item
          }, [])
        }else { 
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.从要传递的参数中去掉这些重复的
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'platformCityGoodsId')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [importStore 引入门店]
       * @return {[type]} [description]
       */
      importStore() {
        if(this.checkedList.length === 0) {
          this.$message("请选择商品")
          return
        }
        let _param = {
          title: '批量发布至门店及时达',
          platformId: this.searchContent.platformId,
          regionCode: this.searchContent.regionCode
        }
        this.$refs.chooseStore.setDefault(_param)
      },
      /**
       * [showStoreState 查看门店状态]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      showStoreState(_row) {
        let platformCityGoodsId = _row.platformCityGoodsId
        this.$router.push('/store_timely?platformCityGoodsId=' + platformCityGoodsId)
      },
      /**
       * [doSetMarkTypeConfirmHandel 设置角标]
       * @return {[type]} [description]
       */
      doSetMarkTypeConfirmHandel() {
        if(this.setMarkTypeParams.topMark) {
          if(this.setMarkTypeParams.topMark.length > 6) {
            this.$message("角标限制6个字")
            return
          }
        }
        if(this.setMarkTypeParams.bottomMark) {
          if(this.setMarkTypeParams.bottomMark.length > 10) {
            this.$message("底标限制10个字")
            return
          }
        }

        let platformCityGoodsIdsArr = []
        for(let item of this.checkedList) {
          platformCityGoodsIdsArr.push(item.platformCityGoodsId)
        }
        this.setMarkTypeParams.platformCityGoodsIdList = platformCityGoodsIdsArr
        this.isDisabled = true
        this.$http.post(presaleGoodsMgt.saveMark, {...this.setMarkTypeParams, DIFFCITY: true}).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("设置标签成功")
            this.dialogFormVisible = false
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })

      },
      /**
       * [doDelMarkTypeConfirmHandel 删除标签事件]
       * @return {[type]} [description]
       */
      doDelMarkTypeConfirmHandel() {
        let platformCityGoodsIdsArr = []
        for(let item of this.checkedList) {
          platformCityGoodsIdsArr.push(item.platformCityGoodsId)
        }
        this.delMarkTypeParams.platformCityGoodsIdList = platformCityGoodsIdsArr

        if(this.delMarkTypeParams.isDeleteBottomMark === false) {
          this.delMarkTypeParams.isBottomMark = 0
        }else if(this.delMarkTypeParams.isDeleteBottomMark === true){
          this.delMarkTypeParams.isBottomMark = 1
        }

        if(this.delMarkTypeParams.isDeleteTopMark === false) {
          this.delMarkTypeParams.isTopMark = 0
        }else if(this.delMarkTypeParams.isDeleteTopMark === true){
          this.delMarkTypeParams.isTopMark = 1
        }
        this.isDisabled = true
        this.$http.post(presaleGoodsMgt.updateMark, {...this.delMarkTypeParams, DIFFCITY: true}).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("删除标签成功")
            this.dialogDelFormVisible = false
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [showBatchUpdatePrice 批量改价]
       * @return {[type]} [description]
       */
      // showBatchUpdatePrice() {
      //   let _value = {
      //     title: '批量改价',
      //     saleType: this.searchContent.saleType,
      //     platformId: this.searchContent.platformId
      //   }
      //   this.$refs.batchUpdatePrice.setDefaultMesg(_value)
      // },
      /**
       * [DoSelectGoodsHandel 确认选择]
       */
      DoSelectGoodsHandel(_params) {
        let checkedList = _params.checkedList
        let queryString = _params.queryString
        let platformCityGoodsIds = []
        for(let item of checkedList) {
          platformCityGoodsIds.push(item.platformCityGoodsId)
        }
        this.searchContent.platformCityGoodsIds = platformCityGoodsIds
        this.searchContent.queryString = queryString
        this.doSearchHandel(true)
      },
      /**
       * [batchAddGoods 批量添加商品]
       * @return {[type]} [description]
       */
      batchAddGoods() {
        let _value = {
          title: '批量添加',
          saleType: this.searchContent.saleType,
          platformId: this.searchContent.platformId,
          regionCode: this.searchContent.regionCode,
          DIFFCITY: this.searchContent.DIFFCITY
        }
        this.$refs.batchAddGoods.setDefaultMesg(_value)
      },
      /**
       * @description: 导出商品
       * @param {type} 
       * @return: 
       */
      exportData() {
        let filename = `${utils.timestampToTimeNoHMS(Date.parse(new Date()))}_区域商品`
        let exportConfig = {
          platformCityGoodsId: '区域商品id',
          goodsNo: 'SKU编码',
          skuNo: 'ERP编码',
          goodsName: '区域商品名称',
          goodsId: '商品id',
          spec: '规格',
          categoryName: '分类',
          price: '区域售价',
          bigImageUrl: '商品大图URl'
        }
        this.exportLoading = true
        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]
        this.$http.post(presaleGoodsMgt.presaleSearch, {
          ...this.searchContent
        }).then(res => {
          this.exportLoading = false
          if (res.code === 0) {
            if (res.data && res.data.result && res.data.result.length) {
              res.data.result.forEach(item => {
                item.platformCityGoodsName = item.goodsName
                item.presalePrice = item.price
                item.preSaleImageUrl = item.bigImageUrl
              })
              let tableDataArr = res.data.result
              const tHeader = utils.objectToArray(exportConfig, false)
              const filterVal = utils.objectToArray(exportConfig, true)
              exportSheet.ExportJsonToExcel({
                header: tHeader,
                filterVal,
                data: tableDataArr,
                filename: filename
              })
              this.$notify.success({
                title: '温馨提示',
                message: `${filename}导出成功`
              });
            } else {
              this.$notify.warning({
                title: '温馨提示',
                message: '没有查询到要导出的数据'
              });
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.exportLoading = false
          this.$notify.error({
            title: '提示(导出数据)：',
            message: e.message
          })
        })
      },
      /**
       * @description: 引入门店弹窗组件点击确认
       * @param {type} 
       * @return: 
       */
      confirmHandel(val) {
        let storeNos = val.checkedList.map(item => {
          return item.storeNo
        })
        let platformCityGoodsIdListArr = this.checkedList.map(item => {
          return item.platformCityGoodsId
        })
        let params = {
          platformCityGoodsIdList: platformCityGoodsIdListArr,
          platformId: this.searchContent.platformId,
          regionCode: this.searchContent.regionCode,
          storeNoList: storeNos,
          DIFFCITY: true
        }
        this.$http.post(presaleGoodsMgt.batchStoreImport, {...params}).then(res => {
          if(res.code === 0) {
            this.$refs.chooseStore.closeDialog()
            this.$refs.chooseStore.closeDisabled()
            this.$message.success("引入门店成功")
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$refs.chooseStore.closeDisabled()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * @description: 批量添加顺丰标品
       * @param {type} 
       * @return: 
       */
      batchAddSFGoods() {
        if(this.$refs.excelAdd) {
          let val = {
            title: 'Excel 批量导入',
            tempConfig: this.tempConfig
          }
          this.$refs.excelAdd.setDefaultMsg(val)
        }
      },
      doConfirmHandel(value) {
        let goodsExcelReqs = value.dataList || []
        goodsExcelReqs.forEach(item => {
          item.vendorType = 2
        })
        let params = {
          platformId: 5,
          regionCode: this.searchContent.regionCode,
          saleType: 4,
          goodsExcelReqs,
          DIFFCITY: this.searchContent.DIFFCITY
        }
        this.$http.post(presaleGoodsMgt.checkSFBatchInsert, params).then(res => {
          this.$refs.excelAdd.closeDisabled()
          this.$refs.excelAdd.closeDialog()
          if(res.code === 0) {
            this.hasErrorMsgData = res.data && res.data.cityGoodsVos && res.data.cityGoodsVos.length ? res.data.cityGoodsVos : []
            let isOk = this.hasErrorMsgData.some(item => {
              return item.msgType === 1
            })
            if(isOk) {
              this.showTipErrorMsg = true
            }else {
              // 批量添加
              this.$http.post(presaleGoodsMgt.batchInsert, params).then(res => {
                this.$refs.excelAdd.closeDisabled()
                this.$refs.excelAdd.closeDialog()
                if(res.code === 0) {
                  this.$message.success("批量添加成功")
                  this.isShow = false
                  this.doSearchHandel()
                  return
                }
                throw new Error(res.msg)
              }).catch(e => {
                this.$refs.excelAdd.closeDisabled()
                this.$refs.excelAdd.closeDialog()
                this.$notify.error({
                  title: '提示',
                  message: e.message
                })
              })
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$refs.excelAdd.closeDisabled()
          this.$refs.excelAdd.closeDialog()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      downLoadErrorMsg() {
        let downloadExcelConfig = {...this.tempConfig}
        downloadExcelConfig.msgTypeStr = '验证结果'
        downloadExcelConfig.msg = '失败原因'
        this.hasErrorMsgData.forEach(item => {
          item.msgTypeStr = item.msgType === 1 ? '验证失败' : '验证成功'
        })
        const tHeader = utils.objectToArray(downloadExcelConfig, false)
        const filterVal = utils.objectToArray(downloadExcelConfig, true)
        exportSheet.ExportJsonToExcel({
          header: tHeader,
          filterVal,
          data: this.hasErrorMsgData,
          filename: `Excel批量导入模板`
        })
        setTimeout(() => {
          this.$notify.success({
            title: '温馨提示',
            message: `Excel批量导入模板下载成功`
          })
          this.isDisabled = false
        }, 500)
      },
      isShowTipHandel() {
        this.isShowTip = false
      }
    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      from.meta.keepAlive = false;
      next();
    }

  }
</script>


